




































































//用于首页点圈图
import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from '@/components/table/index.vue';
@Component({
  components: {
    tableinfo
  }
})
export default class graph extends Vue {
  private handleTabClick(e: any) {
    if (e.name === 'second') {
      this.getHistoryWarning()
    } else {
      this.getActiveWarning()
    }
  }
  private activeName: string = 'first';
  private option: any = {
    tableData: [],
    tableCol: [
      {
        label: '类型',
        prop: 'alarm_type_id'
      },
      {
        label: '内容',
        prop: 'alarm_msg'
      },
      {
        label: '时间',
        prop: 'alarm_time'
      }
    ],
    limit: 10,
    pageSize: 0
  }
  private option1: any = {
    tableData: [],
    tableCol: [
      {
        label: '类型',
        prop: 'alarm_type_id'
      },
      {
        label: '内容',
        prop: 'alarm_msg'
      },
      {
        label: '时间',
        prop: 'alarm_time'
      }
    ],
    limit: 10,
    pageSize: 0
  }
  private option2: any = {
    tableData: [],
  }
  private curData: any = null;
  private watchingDialogVisible = false;
  private deviceDialogVisible = false;
  private warningDialogVisible = false;
  private curPage: number = 1;
  private curPage1: number = 1;
  private curPage2: number = 1;
  private warningDetailClick() {
    this.getActiveWarning();
    this.warningDialogVisible = true;
  }
  private async getHistoryWarning() {
    let res = await this.$http.get('python/alarm_list', {
      "action": "findData",
      "limit": 10,
      "page": this.curPage1,
      "whereJson": {"client_ip": this.curData.ip, "status": 0},
    })
    this.option1.pageSize = res.count;
    this.option1.tableData = res.rows;
  }
  private async getActiveWarning() {
    let res = await this.$http.get('python/alarm_list', {
      "action": "findData",
      "limit": 10,
      "page": this.curPage,
      "whereJson": {"client_ip": this.curData.ip, "status": 1},
    })
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  private changePage() {
    this.getActiveWarning();
  }
  private changePage1() {
    this.getHistoryWarning();
  }
  private deviceDetailClick() {
    this.deviceDialogVisible = true;
  }
  private watchingDetailClick() {
    this.watchingDialogVisible = true;
  }
  private changePage2() {
    this.getHistoryWarning();
  }
  private trash: any;
  private showMenu = false;
  private menu_x: number = 0;
  private menu_y: number = 0;
  private title: string = "设备总览";
  private time: string = new Date().toString().slice(0, 25);
  private serverGreen: any = require('../../assets/img/graph/server_green.png');
  private serverRed: any = require('../../assets/img/graph/server_red.png');
  // @param canvasWidth 宽
  // @param canvasHeight 高
  // @param data 数据（直接传他给的就行了
  // @param tableData 表格数据
  // @param searchForm 搜索数据
  // @param pageSize 单页数量
  // @param pageLimit 总数
  // @param searchOperate 查询列条件
  @Prop() public canvasWidth!: number;
  @Prop() public canvasHeight!: number;
  @Prop() public data!: any;
  //   产生随机颜色
  //data :数据
  //   name: 名字
  //   status:一般正常  1 蓝色，故障  2 黄色，离线   3 灰色
  //   value
  private seriesData(data: any[]): any {
    let arr: any[] = [
        {
          name: "infoblox",
          value: 20,
          symbolSize: 30,
          label: {
            show: true
          },
          itemStyle: {
            color: "#66ccff"
          }
        }
      ],
      arr1 = [];
    let area: any = {};
    for (let i = 0; i < data.length; i++) {
      if (data[i].level in area) {
        area[data[i].level].push(data[i]);
      } else {
        area[data[i].level] = [data[i]];
      }
    }
    for (let k in area) {
      arr.push({
        name: k,
        symbolSize: 25,
        label: {
          show: true
        },
        itemStyle: {
          color: "#ffd700"
        }
      });
      arr1.push({
        source: k,
        target: "infoblox"
      });
      for (let j = 0; j < area[k].length; j++) {
        arr.push({
          name: area[k][j].name,
          symbolSize: 30,
          label: {
            show: true
          },
          symbol: `image://${this.serverGreen}`,
            // "image://http://183.247.173.159:49982/dnsmgr/static/images/sever_green.png"
          customData: area[k][j]
        });
        arr1.push({
          source: area[k][j].name,
          target: k
        });
      }
    }
    return {
      name: this.title,
      type: "graph",
      layout: "force",
      data: arr,
      links: arr1,
      roam: true,
      // tooltip: {
      //   show: true,
      //   formatter: function(params:any) {
      //   return '<p style="font-size:18px">' + params.name + '</p><p style="font-size:14px" @click="console.log(123)" >这里可以写一些，想展示在页面上的别的信息</p>'
      //     // return params.name + "：" + params.data["value"] + "%";
      //   }
      // },
      draggable: true,
      label: {
        normal: {
          position: "right"
        },
        show: false
      },
      force: {
        repulsion: 1000
      }
    };
  }
  @Watch("data") private change(newTxt: number, oldTxt: number) {
    let option: object = {
      title: {
        text: this.title,
        top: "top",
        left: "center"
      },
      tooltip: {
        show: false
      },
      // animation: true,
      series: this.seriesData(this.data)
    };
    var echarts = require("echarts");
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("graph"));
    // 绘制图表
    myChart.setOption(option);
    myChart.on('contextmenu', (data: any) => {
      if (data.data.symbol) {
        this.curData = data.data.customData;
        this.menu_x = data.event.offsetX;
        this.menu_y = data.event.offsetY;
        this.showMenu = true;
      } else {
        this.showMenu = false;
      }
    })
  }
  created() {
    this.trash = document.oncontextmenu;
    document.oncontextmenu = (e) => {
      return false;
    }
    document.onclick = (e) => {
      this.showMenu = false;
    }
  }
  beforeDestroy() {
    document.oncontextmenu = this.trash
  }
}
