






import Vue from 'vue'
import tableinfo from '@/components/table/index.vue'
import Component from 'vue-class-component';
@Component({
  components: {
    tableinfo
  }
})
export default class historyWarning extends Vue{
  private curPage: number = 1;
  private option: any = {
    tableData: [],
    tableCol: [
      {
        label: 'IP',
        prop: 'client_ip'
      },
      {
        label: '告警类型',
        prop: 'alarm_type_id'
      },
      {
        label: '内容',
        prop: 'alarm_msg'
      },
      {
        label: '第一次告警时间',
        prop: 'first_alarm_time'
      },
      {
        label: '最后告警时间',
        prop: 'last_alarm_time'
      }
    ],
    limit: 10,
    pageSize: 0,
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: async (data: any) => {
          if (data.selectinput_value && !data.selectinput_label) {
            this.$message({
              showClose: true,
              message: "请输入查找项！",
              type: "error"
            });
            return;
          }
          this.curPage = 1;
          this.getHistoryWarning(data);
        }
      }
    ],
    searchForm: [
      {
        label: "告警类型",
        value: "alarm_type_id",
        type: "input",
        prop: "alarm_type_id"
      },
      {
        label: "IP地址",
        value: "ip",
        type: "input",
        prop: "client_ip"
      }
    ],
  }
  private handleChangePage (page: number) {
    this.curPage = page;
    this.getHistoryWarning();
  }
  private async getHistoryWarning(conditions?: any) {
    let whereJson = {
      status: '1',
      group: 'true'
    }
    if (conditions) {
      whereJson = Object.assign(whereJson, conditions);
    }
    let res = await this.$http.get('/python/alarm_list', {
      action: 'findData',
      page: this.curPage,
      whereJson
    });
    this.option.pageSize = res.count
    this.option.tableData = res.rows;
  }
  mounted() {
    this.getHistoryWarning();
  }
}
